.TodoInfo {
  display: flex;
  flex-direction: column;
  width: 1400px;
  height: 200px;
  justify-content: space-between;
  background-color: #acb1f44b;
  border-radius: 50px;
  padding: 20px;
  cursor: pointer;

  &:hover {
    background-color: rgba(103, 132, 213, 0.49);
  }

  &__title {
    margin: 0;
  }

  &__status {
    display: flex;
    margin: 5px 0;
    gap: 5px;
  }

  &__status-title {
    margin: 0;
  }
}

.completed {
  color: green;
}

.notcomleted {
  color: red;
}
